export const videos = rawVideos
  .map(v => ({ ...v, title: a[v.id] }))
  .sort((a, b) => b.score - a.score)

const doesVideoMatchTagsStates = (tagsStates, video) => {
  const { "": hasDescription, ...restTags } = tagsStates;
  const { description, tagsIds: videoTagsIds } = video; 

  if ((description === null) === hasDescription) 
    return false;
  for (const [tagId, value] of Object.entries(restTags))
    if (!videoTagsIds.includes(tagId) === value)
      return false;

  return true;
};

export const allVideosIds = videos.map(video => video.id); 
export const getVideosIdsByTagsStates = (tagsStates) => allVideosIds.filter(videoId => doesVideoMatchTagsStates(tagsStates, getVideoById(videoId)));
export const getVideoById = id => videos.find(video => video.id === id);
